import React, { Component } from "react";
import Axios from "axios";
import APPCONFIG from "../../../Constants/appConfig";
import { getUserCredential } from "../../../lib/authentication-helper";

export default class Personal_Details extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        firstName: "",
        middleName: "",
        lastName: "",
        caste: "",
        dob: "",
        dobTime: "",
      },
    };
  }
  componentDidMount() {
    let path = "/user/get-details?type=personal";
    let token = getUserCredential();
    var config = {
      headers: { Authorization: token.authToken },
    };
    Axios.get(APPCONFIG.baseAPIUrl + `${path}`, config)
      .then((data) => {
        if (data.error) {
          this.setState({ ...this.state, error: data.error, loading: false });
          alert("Error");
        } else {
          console.log("post", data.data.data);
          // alert("Data added successfully");
          if (data.data.data && data.data.data) {
            let responseData = {};
            let {
              fatherName,
              motherName,
              gender,
              religion,
              nationality,
              isBLPOrEWS,
            } = data.data.data;
            responseData = {
              fatherName,
              motherName,
              motherName,
              gender,
              religion,
              nationality,
              isBLPOrEWS,
            };

            this.props.setPersonalDetail(responseData);
            if (data.data.data.user) {
              let {
                firstName,
                middleName,
                lastName,
                caste,
                dob,
              } = data.data.data.user;
              let { dobTime } = data.data.data;
              let user = {
                firstName,
                middleName,
                lastName,
                caste,
                dob,
                dobTime,
              };
              this.setState({ user });
            }
          }
        }
      })
      .catch(console.log("Register request failed"));
  }

  render() {
    const {
      fatherName,
      motherName,
      gender,
      religion,
      isBLPOrEWS,
      nationality,
    } = this.props.personalDetail;
    return (
      <div>
        <div className="row">
          <div className="col-lg-12">
            <div className="row float-right">
              <a
                href="#instructions"
                data-animation="fadein"
                data-plugin="custommodal"
                className="btn btn-danger btn-round  waves-effect waves-light"
                data-overlaySpeed="200"
                data-overlayColor="#36404a"
              >
                <i className="mdi mdi-information"></i>
                <span>Instructions</span>
              </a>
            </div>
          </div>
        </div>

        <div className="personal-details">
          <div className="col-lg-12">
            <form className="form my-4" action="">
              <div id="reg-user" className="row">
                <div className="col-lg-12">
                  <div className="name-title">
                    <label className="input-title mt-0" htmlFor="name">
                      Candidate's Name<span className="star">*</span>
                    </label>
                  </div>
                  <div className="col-lg-4">
                    <div className="form-group">
                      <div className="input-group mb-3">
                        <div className="input-group-prepend">
                          <span className="input-group-text" id="basic-addon1">
                            <i className="mdi mdi-account-outline font-16"></i>
                          </span>
                        </div>
                        <input
                          type="text"
                          className="form-control"
                          id="username"
                          placeholder="Enter First Name"
                          value={this.state.user.firstName}
                          required
                          disabled
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <div className="form-group">
                      <div className="input-group mb-3">
                        <div className="input-group-prepend">
                          <span className="input-group-text" id="basic-addon1">
                            <i className="mdi mdi-account-outline font-16"></i>
                          </span>
                        </div>
                        <input
                          type="text"
                          className="form-control"
                          id="username"
                          placeholder=""
                          disabled
                          value={this.state.user.middleName}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <div className="form-group">
                      <div className="input-group mb-3">
                        <div className="input-group-prepend">
                          <span className="input-group-text" id="basic-addon1">
                            <i className="mdi mdi-account-outline font-16"></i>
                          </span>
                        </div>
                        <input
                          type="text"
                          className="form-control"
                          id="username"
                          placeholder="Enter Last Name"
                          required
                          disabled
                          value={this.state.user.lastName}
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12">
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="father-name">
                      Father's Name<span className="star">*</span>
                    </label>
                    <div className="form-group">
                      <div className="input-group mb-3">
                        <div className="input-group-prepend">
                          <span className="input-group-text" id="basic-addon1">
                            <i className="mdi mdi-account-outline font-16"></i>
                          </span>
                        </div>
                        <input
                          type="text"
                          className="form-control"
                          id="f-name"
                          placeholder="Enter Father's Name"
                          required
                          value={fatherName}
                          onChange={(e) =>
                            this.props.handlePersonalDetailsChange(
                              e,
                              "fatherName"
                            )
                          }
                        />
                        <span className="help-block with-errors"></span>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="m-name">
                      Mother's Name<span className="star">*</span>
                    </label>
                    <div className="form-group">
                      <div className="input-group mb-3">
                        <div className="input-group-prepend">
                          <span className="input-group-text" id="basic-addon1">
                            <i className="mdi mdi-account-outline font-16"></i>
                          </span>
                        </div>
                        <input
                          type="text"
                          className="form-control"
                          id="m-name"
                          placeholder="Enter Mother's Name"
                          required
                          value={motherName}
                          onChange={(e) =>
                            this.props.handlePersonalDetailsChange(
                              e,
                              "motherName"
                            )
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="gender">
                      Gender<span className="star">*</span>
                    </label>
                    <select
                      className="form-control mb-3 custom-select"
                      id="gender"
                      value={gender}
                      onChange={(e) =>
                        this.props.handlePersonalDetailsChange(e, "gender")
                      }
                    >
                      <option>Select Gender</option>
                      <option>Male</option>
                      <option>Female</option>
                      <option>Others</option>
                    </select>
                  </div>
                </div>
                <div className="col-lg-12">
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="caste">
                      Caste<span className="star">*</span>
                    </label>
                    <select
                      className="form-control mb-3 custom-select"
                      disabled
                      value={this.state.user.caste}
                    >

                      <option value="1">General</option>
                      <option value="2">OBC/MOBC</option>
                      <option value="3">SC</option>
                      <option value="4">ST(P)</option>
                      <option value="5">ST(H)</option>
                    </select>
                  </div>
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="dob">
                      Date Of Birth<span className="star">*</span>
                    </label>
                    <div className="form-group mb-0">
                      <div>
                        <div className="input-group">
                          <div className="input-group-prepend">
                            <span
                              className="input-group-text"
                              id="basic-addon1"
                            >
                              <i className="mdi mdi-calendar font-16"></i>
                            </span>
                          </div>
                          <input
                            type="text"
                            className="form-control"
                            // placeholder="01-12-1987"
                            id="date"
                            disabled
                            value={this.state.user.dob}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="dob">
                      Age As On 1st January, 2020
                    </label>
                    <div className="form-group mb-0">
                      <div>
                        <div className="input-group">
                          <div className="input-group-prepend">
                            <span
                              className="input-group-text"
                              id="basic-addon1"
                            >
                              <i className="mdi mdi-calendar font-16"></i>
                            </span>
                          </div>
                          <input
                            type="text"
                            className="form-control"
                            // placeholder="33 Years 12 Months 31 Days"
                            id="date1"
                            disabled
                            value={this.state.user.dobTime}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12">
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="religion">
                      Religion<span className="star">*</span>
                    </label>
                    <div className="form-group mb-0">
                      <select
                        className="form-control mb-3 custom-select"
                        id="religion"
                        required
                        value={religion}
                        onChange={(e) =>
                          this.props.handlePersonalDetailsChange(e, "religion")
                        }
                      >
                        <option>Select Religion</option>
                        <option>Hinduism</option>
                        <option>Islam</option>
                        <option>Christianity</option>
                        <option>Sikh</option>
                        <option>Others</option>
                      </select>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="nation">
                      Nationality<span className="star">*</span>
                    </label>
                    <div className="form-group mb-0">
                      <select
                        className="form-control mb-3 custom-select"
                        id="national"
                        required
                        value={nationality}
                        onChange={(e) =>
                          this.props.handlePersonalDetailsChange(
                            e,
                            "nationality"
                          )
                        }
                      >
                        <option>Select Nationality</option>
                        <option>Indian</option>
                        <option>Others</option>
                      </select>
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <label className="input-title mt-0" htmlFor="bpl">
                      Do You Have BPL/EWS Certificate ?
                      <span className="star">*</span>
                    </label>
                    <div className="form-group mb-0">
                      <select
                        className="form-control mb-3 custom-select"
                        id="bpl"
                        value={isBLPOrEWS ? "Yes" : "No"}
                        onChange={(e) =>
                          this.props.handlePersonalDetailsChange(
                            e,
                            "isBLPOrEWS"
                          )
                        }
                      >
                        <option>Select Yes/No</option>
                        <option value="Yes">Yes</option>
                        <option value="No">No</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}
