import React, { Component } from "react";
import Axios from "axios";
import APPCONFIG from "../../../Constants/appConfig";
import { getUserCredential } from "../../../lib/authentication-helper";

export default class Preferences extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    let path = "/user/get-details?type=preference";

    let token = getUserCredential();
    var config = {
      headers: { Authorization: token.authToken },
    };
    Axios.get(APPCONFIG.baseAPIUrl + `${path}`, config)
      .then((data) => {
        if (data.error) {
          this.setState({ ...this.state, error: data.error, loading: false });
          alert("Error");
        } else {
          console.log("dta", data.data.data);
          // alert("Data added successfully");
          let responseData = {}
          responseData["preference1"] = data.data.data.preference1
          responseData["preference2"] = data.data.data.preference2
          responseData["preference3"] = data.data.data.preference3

          this.props.setPreference(responseData);
        }

      })
      .catch(console.log("Register request failed"));
  }

  render() {
    console.log("----preference props---", this.props.preference)
    const { preference1, preference2, preference3 } = this.props.preference
    return (
      <div>
        <div className="row">
          <div className="col-lg-12">
            <div className="row float-right">
              <a
                href="#instructions"
                data-animation="fadein"
                data-plugin="custommodal"
                className="btn btn-danger btn-round  waves-effect waves-light"
                data-overlaySpeed="200"
                data-overlayColor="#36404a"
              >
                <i className="mdi mdi-information"></i>
                <span>Instructions</span>
              </a>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-lg-12 pl-0 pr-0">
            <div className="card mt-3 apply-online shadow-none">
              <div className="card-body pl-0 pr-0 pt-0">
                <div className="">
                  <form className="form" role="form" data-toggle="validator">
                    <div className="table-responsive-sm mt-0 first">
                      <table className="table table-post mb-0">
                        <thead>
                          <tr>
                            <th scope="col">
                              <div className="pt-4 pb-0 mb-0">
                                <label for="select-all">Preferences</label>
                              </div>
                            </th>

                            <th scope="col">
                              <div className=" pt-4 pb-0 mb-0">
                                <div className="pb-0 mb-0">
                                  <div className="float-left mt-2">
                                    <label>Select Posts</label>
                                  </div>
                                  <div className="float-right">
                                    <button
                                      type="button"
                                      className="next btn btn-primary waves-effect waves-light"
                                      id="reset_pref"
                                    // onClick={this.props.handleReset}
                                    >
                                      Reset Preferences
                                    </button>
                                  </div>
                                </div>
                              </div>
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr className="table-secondary">
                            <th scope="row">
                              <div className="pt-2 pb-0 mt-2 mb-0">
                                <label for="post1">
                                  <h5>Preference 1</h5>
                                </label>
                              </div>
                            </th>

                            <td className="">
                              <select
                                className="form-control custom-select po"
                                id="pos"
                                value={preference1}
                                onChange={(e) => this.props.handleChangePreference(e, 'preference1')}
                              >
                                <option disabled selected value>
                                  Select Post
                                </option>
                                <option value="Post 1">Post 1</option>
                                <option value="Post 2">Post 2</option>
                                <option value="Post 3">Post 3</option>
                              </select>
                            </td>
                          </tr>
                          <tr className="">
                            <th scope="row">
                              <div className="pt-2 pb-0 mt-2 mb-0">
                                <label for="post2">
                                  <h5>Preference 2</h5>
                                </label>
                              </div>
                            </th>

                            <td className="">
                              <select
                                className="form-control custom-select po"
                                id="pos"
                                value={preference2}
                                onChange={(e) => this.props.handleChangePreference(e, 'preference2')}
                              >
                                <option disabled selected value>
                                  Select Post
                                </option>
                                <option value="Post 1">Post 1</option>
                                <option value="Post 2">Post 2</option>
                                <option value="Post 3">Post 3</option>
                              </select>
                            </td>
                          </tr>
                          <tr className="table-secondary">
                            <th scope="row">
                              <div className=" pt-2 pb-0 mt-2 mb-0">
                                <label for="post3">
                                  <h5>Preference 3</h5>
                                </label>
                              </div>
                            </th>

                            <td className="">
                              <select
                                className="form-control custom-select po"
                                id="pos"
                                value={preference3}
                                onChange={(e) => this.props.handleChangePreference(e, 'preference3')}
                              >
                                <option disabled selected value>
                                  Select Post
                                </option>
                                <option value="Post 1">Post 1</option>
                                <option value="Post 2">Post 2</option>
                                <option value="Post 3">Post 3</option>
                              </select>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
