import React, { Component } from "react";
import Layout from "../../components/Admin/layout";
import SideNav from "../../components/Admin/sideNav";
import { fetchDashboardData } from "../../service";

class IndexAdmin extends Component {
  state = {
    data: [],
    isLoading: true,
  };

  async componentDidMount() {
    const data = await fetchDashboardData();
    this.setState({ data }, () => this.setState({ isLoading: false }));
  }

  greeting = () => {
    const myDate = new Date();
    const hrs = myDate.getHours();

    let greet = "";
    if (hrs < 12) {
      greet = "Good Morning";
    } else if (hrs >= 12 && hrs <= 17) greet = "Good Afternoon";
    else if (hrs >= 17 && hrs <= 24) greet = "Good Evening";

    return greet;
  };

  render() {
    const { data, isLoading } = this.state;

    // remove this afterwards, just put loader
    const INITIAL_VALUE = "0";
    const INITIAL_TABLE_DATA = (
      <tr>
        <td>-</td>
        <td>-</td>
        <td>-</td>
        <td>-</td>
      </tr>
    );

    return (
      <Layout>
        <div>
          <div className="page-wrapper-img">
            <div className="page-wrapper-img-inner">
              <div className="sidebar-user media">
                <div className="media-body">
                  <h5 className="text-light">Marie</h5>
                  <h6 className="text-light">Admin</h6>
                </div>
              </div>

              <div className="row mid-page-title">
                <div className="col-sm-12">
                  <div className="page-title-box">
                    <div className="float-right align-item-center mt-2">
                      <div className="alert alert-info border-0" role="alert">
                        <strong>{this.greeting()}</strong>
                      </div>
                    </div>
                    <h4 className="page-title mb-2">
                      <i className="mdi mdi-table-column mr-2"></i>WELCOME TO
                      ONLINE RECRUITMENT PROCESS
                    </h4>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="page-wrapper">
            <div className="page-wrapper-inner">
              {<SideNav />}

              <div className="page-content">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-lg-4">
                      <div className="card overflow-hidden">
                        <div className="card-body mb-0 bg-gradient1">
                          <div className="row">
                            <div className="">
                              <div className="card-icon">
                                <i className="far fa-user"></i>
                              </div>
                              <h4 className="text-white mb-0 font-16">
                                Total Registered
                              </h4>
                              <h2 className="font-weight-bold text-white">
                                {!isLoading
                                  ? data[0].registered
                                  : INITIAL_VALUE}
                              </h2>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="col-lg-4">
                      <div className="card overflow-hidden">
                        <div className="card-body mb-0 bg-gradient3">
                          <div className="row">
                            <div className="">
                              <div className="card-icon">
                                <i className="far fa-user"></i>
                              </div>
                              <h4 className="text-white mb-0 font-16">
                                Total Applied
                              </h4>
                              <h2 className="font-weight-bold text-white">
                                {!isLoading ? data[0].applied : INITIAL_VALUE}
                              </h2>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-4">
                      <div className="card overflow-hidden">
                        <div className="card-body bg-gradient2">
                          <div className="row">
                            <div className="">
                              <div className="card-icon">
                                <i className="far fa-user"></i>
                              </div>
                              <h4 className="text-white mb-0 font-16">
                                Today Applied
                              </h4>
                              <h2 className="font-weight-bold text-white">
                                {!isLoading ? data[0].applied : INITIAL_VALUE}
                              </h2>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-12">
                      <div className="card">
                        <div className="card-body">
                          <h4 className="mt-0 header-title">DAILY UPDATE</h4>
                          <p className="text-muted mb-4">
                            Date Wise Candidate Data{" "}
                          </p>

                          <table
                            id="datatable-monitor"
                            className="table table-striped table-bordered dt-responsive nowrap"
                            style={{
                              borderCollapse: "collapse",
                              borderSpacing: "0",
                              width: "100%",
                            }}
                          >
                            <thead>
                              <tr>
                                <th>SL. no.</th>
                                <th>Date</th>
                                <th>Registered</th>
                                <th>Applied</th>
                              </tr>
                            </thead>
                            <tbody>
                              {!isLoading
                                ? data.map((each, i) => (
                                    <tr>
                                      <td>{i + 1}</td>
                                      <td>{each.date}</td>
                                      <td>{each.registered}</td>
                                      <td>{each.applied}</td>
                                    </tr>
                                  ))
                                : INITIAL_TABLE_DATA}
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}

export default IndexAdmin;
