import React, { Component } from "react";
import { withRouter } from "react-router";
import Axios from "axios";
import APPCONFIG from "../../../Constants/appConfig";
import { getUserCredential } from "../../../lib/authentication-helper";

class Post_Details extends React.Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    
    let path = "/user/get-details?type=post";
    let token = getUserCredential();
    var config = {
      headers: { Authorization: token.authToken },
    };
    Axios.get(APPCONFIG.baseAPIUrl + `${path}`, config)
      .then((data) => {
        if (data.error) {
          this.setState({ ...this.state, error: data.error, loading: false });
          alert("Error");
        } else {
          console.log("post", data.data.data.post);
          // alert("Data added successfully");
          if (data.data.data.post && data.data.data.post.length) {
            let responseData = {};
            if (data.data.data.post.includes("AE") && data.data.data.post.includes("AA")) {
              responseData["postAll"] = true
              responseData["post1"] = true;
              responseData["post2"] = true;

            }
            else if (data.data.data.post.includes("AE")) {
              responseData["post1"] = true;
            }
            else if (data.data.data.post.includes("AA")) {
              responseData["post2"] = true;
            } else {

            }

            this.props.setPostDetail(responseData);
          }
        }
      })
      .catch(console.log("Register request failed"));
  }

  render() {
    console.log("props", this.props);
    return (
      <>

        <div className="row">

          <div className="col-lg-12">
            <div className="row float-right">
              <a
                href="#instructions"
                data-animation="fadein"
                data-plugin="custommodal"
                className="btn btn-danger btn-round  waves-effect waves-light"
                data-overlaySpeed="200"
                data-overlayColor="#36404a"
              >
                <i className="mdi mdi-information"></i>
                <span>Instructions</span>
              </a>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-12 pl-5 pr-5">
            <div className=" pl-5 pr-5 qua">
              <label className="input-title mt-0" htmlFor="qualification">
                Qualification
              </label>
              <select className="select2 form-control mb-3 custom-select">
                <option>
                  B.E, B.Tech / AMIE in Chemical / Civil / Environmental
                  Engineering
                </option>
                <option>BCom / BBA</option>
              </select>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-lg-12 pl-0 pr-0">
            <div className="card mt-3 apply-online shadow-none">
              <div className="card-body pl-0 pr-0 pt-0">
                <div className="">
                  <form className="form" role="form" data-toggle="validator">
                    <div className="row col-lg-12 mt-4 mb-0 pl-0 pr-0 post-header-post">
                      <h6 className="font-14 pl-0 pr-0">
                        YOUR ARE ELIGIBLE htmlFor THE FOLLOWING POSTS. PLEASE
                        SELECT THE POST(s) YOU WANT TO APPLY htmlFor.
                      </h6>
                    </div>
                    <div className="row col-lg-12 mt-2 mb-0 pl-3 pr-0">
                      <p className="font-14 pl-0 pr-0 text-pink">
                        * Candidate can apply htmlFor multiple posts. *
                      </p>
                    </div>

                    <div className="table-responsive-sm mt-0 first post-scroll">
                      <table className="table table-post mb-0">
                        <thead>
                          <tr>
                            <th scope="col">
                              <div className="checkbox checkbox-primary pt-4 pb-0 mb-0">
                                <input
                                  id="select-all"
                                  name="select-all"
                                  // onClick="toggle(this)"
                                  // value="All"
                                  checked={this.props.postDetails.postAll}
                                  type="checkbox"
                                  data-parsley-group="block2"
                                  onChange={(e) =>
                                    this.props.handleChange(
                                      !this.props.postDetails.postAll,
                                      "postAll"
                                    )
                                  }
                                />
                                <label htmlFor="select-all">
                                  Select All Posts
                                </label>
                              </div>
                            </th>
                            <th scope="col">Vacancies</th>
                            <th scope="col">Pay Scale</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr className="table-secondary">
                            <th scope="row">
                              <div className="checkbox checkbox-primary pt-2 pb-0 mb-0">
                                <input
                                  id="post1"
                                  className="post"
                                  name="post1"
                                  type="checkbox"
                                  checked={this.props.postDetails.post1}
                                  onChange={(e) =>
                                    this.props.handleChange(
                                      !this.props.postDetails.post1,
                                      "post1"
                                    )
                                  }
                                  data-parsley-group="block1"
                                />
                                <label htmlFor="post1">
                                  Assistant Engineer(AE)
                                </label>
                              </div>
                            </th>
                            <td className="">34</td>
                            <td className="">
                              PB-III 14000 - 20000
                              <br />+ GP Rs. 6000
                            </td>
                          </tr>
                          <tr className="">
                            <th scope="row">
                              <div className="checkbox checkbox-primary pt-2 pb-0 mb-0">
                                <input
                                  id="post2"
                                  className="post"
                                  name="post2"
                                  type="checkbox"
                                  checked={this.props.postDetails.post2}
                                  onChange={(e) =>
                                    this.props.handleChange(
                                      !this.props.postDetails.post2,
                                      "post2"
                                    )
                                  }
                                  data-parsley-group="block1"
                                />
                                <label htmlFor="post2">
                                  Administrative Assistant-II(AA-II)
                                </label>
                              </div>
                            </th>
                            <td className="">20</td>
                            <td className="">
                              PB-III 14000 - 20000
                              <br />+ GP Rs. 4000
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(Post_Details);
