  componentDidMount() {
    let path = "/user/get-details?type=contact";
    let token = getUserCredential();
    var config = {
      headers: { Authorization: token.authToken },
    };
    Axios.get(APPCONFIG.baseAPIUrl + `${path}`, config)
      .then((data) => {
        if (data.error) {
          this.setState({ ...this.state, error: data.error, loading: false });
          alert("Error");
        } else {
          console.log("post", data.data.data);
          // alert("Data added successfully");
          if (data.data.data && data.data.data) {
            let responseData = {};
            let {
              alternatePhone,
              permanentAddress,
              district,
              policeStation,
              postOffice,
              pinCode,
              correspondenceAddress,
              district1,
              policeStation1,
              postOffice1,
              pinCode1,
            } = data.data.data;
            responseData = {
              alternatePhone,
              permanentAddress,
              district,
              policeStation,
              postOffice,
              pinCode,
              correspondenceAddress,
              district1,
              policeStation1,
              postOffice1,
              pinCode1,
            };
            this.props.setContactDetails(responseData);
            if (data.data.data && data.data.data.user) {
              let userDetails = {};
              let { email, phone } = data.data.data.user;
              userDetails = {
                email,
                phone,
              };
              this.setState({
                userDetails,
              });
            }
          }
        }
      })
      .catch(console.log("Register request failed"));
  }

