import React, { Component } from 'react';
import Axios from 'axios';
import APPCONFIG from '../../../Constants/appConfig';
import { setUserData } from '../../../lib/authentication-helper';
import { withRouter } from 'react-router-dom';


class Login extends Component {

    state = {
        data: {
            username: null,
            password: null,
        },
        error: '',
        isLoggedIn: false,
        loading: false,
        didRedirect: false

    }



    render() {
        console.log("Data", this.state.data)
        console.log("Props", this.props)
        const handleChange = (e, field) => {
            let { value } = e.target;
            this.setState({
                data: {
                    ...this.state.data,
                    error: false,
                    [field]: value
                }
            });

        }

        const  handleValidation= ()=> {
            let fields = this.state.data;
            let errors = {};
            let formIsValid = true;

            //Email
    if (!fields["username"]) {
        formIsValid = false;
        errors["username"] = "Cannot be empty";
      }
  
      if (fields["username"]) {
        let lastAtPos = fields["username"].lastIndexOf("@");
        let lastDotPos = fields["username"].lastIndexOf(".");
  
        if (
          !(
            lastAtPos < lastDotPos &&
            lastAtPos > 0 &&
            fields["username"].indexOf("@@") == -1 &&
            lastDotPos > 2 &&
            fields["username"].length - lastDotPos > 2
          )
        ) {
          formIsValid = false;
          errors["username"] = "Please Enter Application id/ Email Id/ Mobile Number";
        }
      }
      //password
      if (!fields["password"]) {
        formIsValid = false;
        errors["password"] = "Cannot be empty";
      }
        
            this.setState({ error: errors });
            return formIsValid;
          }
        


        const handleSubmit = (event) => {
            event.preventDefault();

            if (handleValidation()) {
                this.setState({ loading: true });
                makeApiCall(this.state.data)
            } else {
                if (this.state.data.username && this.state.data.password)
                    alert("Enter Valid Mail and Passsword")

            }

        }


        const makeApiCall = (data) => {
            console.log("makeapi")
            Axios.post(APPCONFIG.baseAPIUrl + '/auth/login/', data)
                .then(data => {
                    if (data.error) {
                        this.setState({ ...this.state, error: data.error, loading: false });
                    } else {
                        // this.setState({ ...this.state,  didRedirect: true });
                        console.log("Data 1", data.data)
                        if (data.data.statusCode === 200) {
                            console.log('done logged in')
                            setUserData({ authToken: data.data.data.authToken }, "authToken");
                            this.props.history.push('/userDashboard');
                        }
                        //  this.performRedirect()
                    }
                })
                .catch(console.log("signin request failed"));

        }

        //  performRedirect = () => {
        //     if (this.state.didRedirect) {
        //     //   if (user && user.role === 1) {
        //     //     return <Redirect to="/admin/dashboard" />;
        //     //   } else {
        //     //     return <Redirect to="/user/dashboard" />;
        //             alert("You are login successfully")
        //     //   }
        //     }
        //     // if (isAuthenticated()) {
        //     //   return <Redirect to="/" />;
        //     // }
        //   };

        const { username, password } = this.state
        return (
            <div className="login-ors-log">
                <div className="col-lg-12">
                    <div className="card border mb-0 text-center notice">
                        <div className="card-header bg-gradient4 text-white">
                            <h4 className="mt-0 mb-1">Application Notice</h4>
                        </div>
                        <div className="card-body">
                            <div className="table-responsive">
                                <table className="table mb-0 ">
                                    <tbody>
                                        <tr>
                                            <td><i className="mdi mdi-cellphone text-dark font-20"></i></td>
                                            <td>0361 2652774, 255025 (11:00 AM - 4:30 PM)</td>
                                        </tr>
                                        <tr>
                                            <td><i className="mdi mdi-whatsapp  text-custom1 font-20"></i></td>
                                            <td>+91 98956 56454</td>

                                        </tr>
                                        <tr>
                                            <td><i className="mdi mdi-email-outline text-red  font-20"></i></td>
                                            <td>info@pcbassam.org</td>
                                        </tr>
                                        <tr>
                                            <td><i
                                                className="mdi mdi-file-document-box-outline text-custom2 font-20"></i>
                                            </td>
                                            <td><a href="">Click Here Advertisment<span><i
                                                className="mdi mdi-chevron-right font-16"></i></span></a></td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>

                    </div>

                    <div className="card border mt-2 log_page shadow">
                        <div className="card-header bg-gradient4 text-white text-center">
                            <h4 className="mt-0 mb-1">Login</h4>
                        </div>
                        <div className="card-body">
                            <p className="text-center">Already Registered ?</p>
                            <div className="px-3">
                                <form className="form-horizontal my-4" onSubmit={(e)=>handleSubmit(e)}>
                                    <div className="form-group font-13">
                                        <label htmlFor="username">Registration Id/ Email Id/ Mobile
                                                Number</label>
                                        <div className="input-group mb-3">
                                            <div className="input-group-prepend">
                                                <span className="input-group-text" id="basic-addon1"><i
                                                    className="mdi mdi-account-outline"></i></span>
                                            </div>
                                            <input type="text" className="form-control font-13" id="username"
                                                placeholder="Enter Application id/ Email Id/ Mobile Number"
                                                onChange={(e) => handleChange(e, 'username')}
                                                value={username}
                                                // required
                                                // data-parsley-required-message="Please Enter Application id/ Email Id/ Mobile Number" 
                                                />
                                               
                                        </div>
                                        <span style={{ color: "red" }}>
                              {this.state.error["username"]}
                            </span>
                                    </div>

                                    <div className="form-group font-13">
                                        <label htmlFor="userpassword">Password</label>
                                        <div className="input-group mb-3">
                                            <div className="input-group-prepend">
                                                <span className="input-group-text" id="basic-addon2"><i
                                                    className="mdi mdi-lock-outline"></i></span>
                                            </div>
                                            <input type="password" className="form-control " id="userpassword"
                                                placeholder="Enter Password" required
                                                onChange={(e) => handleChange(e, 'password')}
                                                value={password}
                                                data-parsley-length="[8,20]"
                                                //    data-parsley-pattern="/(?=.\d)(?=.[a-z])(?=.*[A-Z])/"
                                                data-parsley-type="alphanum"
                                                data-parsley-required-message="Should contain min. 1 Capital, 1 Small, 1 Number and 8 - 20 characters ."
                                                autoComplete="off" />
                                        </div>
                                    </div>

                                    <div className="form-group mb-0 row">
                                        <div className="col-12 mt-2">
                                            <button
                                            type="submit"
                                                className="btn btn-primary btn-block waves-effect waves-light"
                                            >Log In <i className="fas fa-sign-in-alt ml-1"></i>
                                            </button>
                                        </div>
                                    </div>
                                    <div className="form-group row mt-4">

                                        <div className="col-sm-12 text-center">
                                            <a href="forgot_pass.html" className="text-muted font-13"><i
                                                className="mdi mdi-lock"></i> Forgot Your Password?</a>
                                        </div>
                                    </div>
                                </form>
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

export default withRouter(Login);